.nav {
    position:fixed;
    padding:10px;
    display:flex;
    flex-direction:column;
    box-shadow: 3px 3px 3px 1px gray;
    top:50px;
}
.nav a {
    text-decoration: none;
    color: #0008fc;
    text-transform: uppercase;
    font-size: 12px;
    text-align: center;
    font-weight: 600;
}